html, body {
  height: 100%;
  width: 100%;
  padding-top: 0px;
}

#root {
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
  /* display: flex; */
  /* flex-wrap: wrap; */
  width: 100vh;
  height: 100vh;
  /* flex-direction: row; */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  /* background-color: #222; */
  background-color: #483D8B;
  height: 15%;
  width: 100%;
  /* padding: 20px; */
  padding: 0px;
  color: white;
  display: block;
  position: fixed;
  z-index: 999;
  /* order: 1; */
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* sideBar */

.toggle-SideBar {
  /* background-color: black; */
  background-color: #8A2BE2;
  position: absolute;
  bottom: 7px;
  left: 7px;
}

.toggle-line {
  width: 20px;
  height: 2px;
  margin: 5px 0;
  background-color: white;
}

.sideBar {
  height: 85vh;
  /* width:30vw; */
  /* max-width: 300px; */
  width: 300px;
  /* order: 2; */
  /* position: absolute; */
  background: #B0C4DE;
  -webkit-box-shadow: 6px 10px 18px #2F4F4F;
          box-shadow: 6px 10px 18px #2F4F4F;
  /* display: flex;
  flex-wrap: wrap; */
  padding-top: 15vh;
  position: absolute;
  z-index: 998;
  padding-left: 0;
}

.close {
  -webkit-transform: translate(-300px, 0);
      -ms-transform: translate(-300px, 0);
          transform: translate(-300px, 0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.location-sideBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
  width: calc(100%-10px);
}

.location-sideBar:hover {
  /* background-color: #FFDAB9 */
  /* background-color: #00FFFF */
  /* background-color: #FFF0F5 */
  background-color: #E6E6FA
}



.chosen {
  /* chosen location of sideBar */
  background-color: #FFC0CB;
  border-style: solid;
  border-width: 1px;
  border-color: #FF00FF;
}

.menubar {
  padding-left: 0;
}

.locationSideBar {
  height: auto;
}

.input {
  width: 100%;
  height: 50px;
  border-color: # 191970;
}
/* map */

.map-container {
  height: 85%;
  padding-top: 30%;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
}
/*infobox*/
.infobox {
  width: auto;
}
.infoBoxContent{
  list-style-type: none;
}
.title{
  text-decoration: underline
}

@media screen and (min-width:850px) {
  .toggle-SideBar {
    display: none;
  }
  .sideBar {
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .map-container {
    width: calc(100% - 300px);
  }
}



